<template>
    <header>
      <v-app-bar app light hide-on-scroll>
        <v-layout justify-center align-center class="header-size">
      <v-app-bar-nav-icon>
        <router-link to="/">
        <v-img v-bind:src="logo_src"></v-img>
        </router-link>
      </v-app-bar-nav-icon>
      <v-spacer></v-spacer>
      <v-item-group
      v-if="showItems">
      <router-link to="/about" class="header-link on-pc">
        About
      </router-link>
      <router-link to="/expriences" class="header-link on-pc">
        Experiences
      </router-link>
      <router-link to="/products" class="header-link on-pc">
        Products
      </router-link>
      <router-link to="/skills" class="header-link on-pc">
        Skills
      </router-link>
      <router-link to="/contact" class="header-link on-pc">
        Contact
      </router-link>
      </v-item-group>
      <v-app-bar-nav-icon
      v-else
      class="d-flex d-md-none"
      @click="drawer = !drawer"></v-app-bar-nav-icon>
      <v-navigation-drawer
      v-model="drawer"
      right
      app
      width="100%"
      fill-height
      temporary>
      <v-list nav dense>
      <v-list-item>
      <router-link to="/about" class="header-link">
        About
      </router-link></v-list-item>
      <v-list-item>
      <router-link to="/expriences" class="header-link">
        Experiences
      </router-link></v-list-item>
      <v-list-item>
      <router-link to="/products" class="header-link">
        Products
      </router-link></v-list-item>
      <v-list-item>
      <router-link to="/skills" class="header-link">
        Skills
      </router-link></v-list-item>
      <v-list-item>
      <router-link to="/contact" class="header-link">
        Contact
      </router-link></v-list-item>
      </v-list>
      </v-navigation-drawer>
        </v-layout>
      </v-app-bar>
    </header>
</template>

<script>
export default {
  name: 'Header',
  data(){
    return{
      logo_src: require("@/assets/logo.png"),
      drawer: false,
      pagewidth: window.innerWidth,
    }
  },
  computed: {
    showItems: function() { 
      while (this.pagewidth < 800){
        return true
      }
      return false
    },
  },
}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/_color.scss';
@import '@/assets/styles/_size.scss';

header {
  width: 100%;
  text-align: center;
  background-color: white;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  position: fixed;
  top: 0;
  z-index: 10;
}

.header-size{
  max-width: 900px;
  margin: 0 auto;
}

.header-link {
  height: $header-height_pc;
  line-height: $header-height_pc;
  transition: all 0.3s;
  cursor: pointer;
  font-size: 18px;
  color: $text-color;
  text-decoration: none;
  width: 150px;
  display: inline-block;

  &:hover {
    background-color: darken(white, 10%);
  }

  &:link {
    color: $text-color;
  }
}
</style>
